.main-form {
    margin-top: calc(5% - 20px);
    background-color: white;
    border-radius: 6px;
    padding: 1% 2%;
    max-width: 600px;
    width: calc(25% - 20px);
}

.container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.main-table {
    margin-top: 20%;
}

.table {
    max-width: 800px;
}

.bus-number-select {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 400;
    margin: 0;
    background: none;
    padding: .40rem 0;
    border: none;
    border-bottom: 1px solid #aaaaaa;
    width: 100%;
}

@media screen and (max-width: 700px) {
    .main-form {
        margin-top: calc(20% - 20px);
        background-color: white;
        border-radius: 6px;
        padding: 2% 3%;
        max-width: 600px;
        width: calc(100% - 20px);
    }

    .main-table {
        margin-top: 5%;
    }

}