.header {
    font-size: 1.2em;
    font-weight: 600;
    background-color: rgb(52, 58, 64);
}

.wrapper {
    max-width: 600px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
    position: relative;
}

.content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 8vh;
}

.content a {
    text-decoration: none;
}

.docs, .profile, .transport, .auth, .logo-link {
    font-size: 18px;
    color: #fff;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
}

.logo-link:hover {
    color: #fff;
}

.auth:hover, .profile:hover, .content nav a:hover {
    color: #ffed6b;
    transition: 1s;
}

nav {
    width: 100%;
}

nav a {
    display: block;
    width: 100%;
    margin: auto;
    text-align: center;
}

.is-active, .is-active:hover {
    color: #ffca64;
}
