.bus-number-select {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 400;
    margin: 0;
    background: none;
    padding: .40rem 0;
    border: none;
    border-bottom: 1px solid #aaaaaa;
    width: 350px;
}

.h5 {
    font-weight: 400;
    font-size: 1.6em;
}

.h6 {
    font-weight: 400;
    font-size: 1.5em;
    padding-top: 10px;
}


@media only screen and (min-width : 350px) and (max-width : 480px) {
    .bus-number-select {
        display: block;
        font-size: 16px;
        font-family: sans-serif;
        font-weight: 400;
        margin: 0;
        background: none;
        padding: .40rem 2.5rem .50rem 1rem;
        border: none;
        border-bottom: 1px solid #aaaaaa;
        width: 280px;
    }
}

@media only screen and (min-width : 300px) and (max-width : 340px) {
    .bus-number-select {
        display: block;
        font-size: 16px;
        font-family: sans-serif;
        font-weight: 400;
        margin: 0;
        background: none;
        padding: .40rem 2.5rem .50rem 1rem;
        border: none;
        border-bottom: 1px solid #aaaaaa;
        width: 240px;
    }

    .h5 {
        font-weight: 400;
        font-size: 1.4em;
    }

    .h6 {
        font-weight: 500;
        font-size: 1.1em;
        padding-top: 10px;
    }
}
